/**
*
* 		WORDPRESS
*			- Core Wordpress Styles
*
**/

.aligncenter {
	display: block;
	margin: 0 auto;
}

.alignleft {
	float: left;
}

.alignright {
	float: right;
}

.wp-caption {
	border: 1px solid #666;
	text-align: center;
	background: #ccc;
	padding: 10px;
	margin: 10px;
}

br.clear {
	clear: both;
	display: block;
	height: 1px;
	margin: -1px 0 0 0;
}